<template>
  <nav class="navbar navbar-expand-lg navbar-absolute"
       :class="{'bg-white': showMenu, 'navbar-transparent': !showMenu}">
    <div class="container-fluid">
      <div class="navbar-wrapper">
        <div class="navbar-toggle d-inline" :class="{toggled: $sidebar.showSidebar}">
          <button type="button" class="navbar-toggler"
                  @click="toggleSidebar">
            <span class="navbar-toggler-bar bar1"></span>
            <span class="navbar-toggler-bar bar2"></span>
            <span class="navbar-toggler-bar bar3"></span>
          </button>
        </div>
        <a class="navbar-brand">{{routeName}}</a>
      </div>
      <button class="navbar-toggler" type="button"
              @click="toggleMenu"
              data-toggle="collapse"
              data-target="#navigation"
              aria-controls="navigation-index"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar navbar-kebab"></span>
        <span class="navbar-toggler-bar navbar-kebab"></span>
        <span class="navbar-toggler-bar navbar-kebab"></span>
      </button>

      <collapse-transition>
        <div class="collapse navbar-collapse show" v-show="showMenu">
          <ul class="navbar-nav" :class="$rtl.isRTL ? 'mr-auto' : 'ml-auto'">
            <div class="search-bar input-group" @click="searchModalVisible = true">
              <!-- <input type="text" class="form-control" placeholder="Search...">
              <div class="input-group-addon"><i class="tim-icons icon-zoom-split"></i></div> -->
              <button class="btn btn-link" id="search-button" data-toggle="modal" data-target="#searchModal">
                <i class="tim-icons icon-zoom-split"></i>
              </button>
              <!-- You can choose types of search input -->
            </div>
            <modal :show.sync="searchModalVisible"
                   class="modal-search"
                   id="searchModal"
                   :centered="false"
                   :show-close="true">
              <input slot="header" v-model="search" type="text" class="form-control" id="inlineFormInputGroup" placeholder="SEARCH" v-on:keyup.enter="searchUser">
            </modal>
            <base-dropdown tag="li"
                           :menu-on-right="!$rtl.isRTL"
                           title-tag="a"
                           class="nav-item"
                           menu-classes="dropdown-navbar">
              <a slot="title" href="#" class="dropdown-toggle nav-link" data-toggle="dropdown" aria-expanded="true">
                <div class="photo">
                  <img src="avatar-placeholder.png">
                </div>
                <b class="caret d-none d-lg-block d-xl-block"></b>
                <p class="d-lg-none">
                  Log out
                </p>
              </a>
              <li class="nav-link">
                <a @click="goToProfile" href="#" class="nav-item dropdown-item">Profile</a>
              </li>
              <li class="nav-link">
                <router-link :to="{name: 'Settings'}">
                  <a href="#" class="nav-item dropdown-item">Settings</a>
                </router-link>
              </li>
              <div class="dropdown-divider"></div>
              <li class="nav-link">
                <a @click="signOut" href="#" class="nav-item dropdown-item">Log out</a>
              </li>
            </base-dropdown>
          </ul>
        </div>
      </collapse-transition>
    </div>
  </nav>
</template>
<script>
  import { CollapseTransition } from 'vue2-transitions'
  import Modal from '@/components/Modal'
  const logger = require('heroku-logger')
  import { getConfig } from 'ln-radiks'

export default {
    components: {
      CollapseTransition,
      Modal
    },
    computed: {
      routeName () {
        const { name } = this.$route
        return this.capitalizeFirstLetter(name)
      },
      isRTL () {
        return this.$rtl.isRTL
      }
    },
    data: () => ({
      activeNotifications: false,
      showMenu: false,
      searchModalVisible: false,
      searchQuery: '',
      blockstack: window.blockstack,
      userSearch: '',
      activeItem: 5,
      search: ''
    }),
    methods: {
      capitalizeFirstLetter (string) {
        return string.charAt(0).toUpperCase() + string.slice(1)
      },
      toggleNotificationDropDown () {
        this.activeNotifications = !this.activeNotifications
      },
      closeDropDown () {
        this.activeNotifications = false
      },
      toggleSidebar () {
        this.$sidebar.displaySidebar(!this.$sidebar.showSidebar)
      },
      hideSidebar () {
        this.$sidebar.displaySidebar(false)
      },
      toggleMenu () {
        this.showMenu = !this.showMenu
      },
      searchUser () {
        logger.info('searching for user: ' + this.search)
        this.$router.push({ path: `/profile/${this.search}/` })
      },
      goToProfile () {
        var userData = this.blockstack.loadUserData()
        var username = userData.username
        logger.info('searching for user: ' + this.search)
        this.$router.push({ path: `/profile/${username}/` })
      },
      signOut () {
        this.blockstack.signUserOut(window.location.href)
        const { userSession } = getConfig()
        userSession.signUserOut()
          .then(() => {
            this.$router.push('/home')
          })
        // this.user = null
        // this.radiksUser = null
      }
    }
  }
</script>
<style>
</style>



